.quotes-container {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 20px;
  margin-bottom: 100px;
  background-image: url("../whitetruck.avif"); 
  background-size: cover; 
  background-repeat: no-repeat; 
  background-position: top; 
  overflow: hidden;
  height: 120vh;
}

.form-container {
  margin-top: 20px; 
  margin-bottom: 20px;
  max-width: 500px;
  padding: 20px;
  border: 3px solid black;
  border-radius: 5px;
  background-color: #ccc;
  overflow-y: auto;
  max-height: 80vh;
  opacity: 0.9;
}

.quote-form {
  display: flex;
  flex-direction: column;
}

.form-group {
  margin-bottom: 20px;
}

label {
  margin-bottom: 5px;
  font-weight: bold;
}

input {
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
  width: 100%;
  box-sizing: border-box;
}

.submit-btn {
  background-color: #b21204;
  color: #black;
  padding: 10px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s;
  font-family: "Righteous", sans-serif;
}

.submit-btn:hover {
  color: #fcfbfb; 
  text-shadow: 0 0 5px rgb(253, 248, 248); 
  background-color: #b21204;
}

.areas {
  text-align: center;
  color: #b21204;
  font-family: "Righteous", sans-serif;
  font-size: x-large;
}

.fontcolor {
  font-family: "Righteous", sans-serif;
}

.geta {
  color: #b21204;
  text-align: center;
  font-size: xx-large;
}
body {
  background-color: black;
  margin: 0;
  padding: 0;
}

.logo {
  position: relative; 
  z-index: 2; 
}

.header {
  background-color: black;
  color: #fff;
  position: relative; 
  z-index: 1;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 10px 20px;
}

.nav {
  flex-grow: 1; 
  display: flex; 
  justify-content: flex-end; 
}

.header ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
  display: flex;
  align-items: center; 
}

.header ul li {
  margin-right: 20px;
}

.header ul li a {
  color: white;
  text-decoration: none;
  font-family: 'Righteous', sans-serif; 
  font-size: large; 
}


.header ul li a:hover {
  color: #b21204; 
  text-shadow: 0 0 5px rgb(255, 254, 254); 
}

.quotes {
  font-size: x-large;
  border: 2px solid #b21204;
  padding: 5px 10px; 
  border-radius: 5px;
  color:white;
  text-decoration: none;
  font-family: 'Righteous', sans-serif;
}
.quotes:hover {
  color: #fcfbfb; 
  text-shadow: 0 0 5px rgb(253, 248, 248); 
  background-color: #b21204;

}
@media only screen and (max-width: 600px) {
  .header {
      flex-direction: column;
  }

  .nav {
      padding-left: 15px;
      padding-top: 5px;
  }
}